.Gallery {
  .loading {
    position: absolute;
    top: 0px;
    left: 50%;
    transform: translateX(-50%);
    z-index: 0;
  }
  figure {
    background: lightskyblue;
  }

  article {
    transform: translateY(100px);
    opacity: 0;
    transition: 1s;

    &.on {
      opacity: 1;
      transform: translateY(0);
    }

    ul {
      width: 100%;

      &::after {
        content: "";
        display: block;
        clear: both;
      }

      li {
        width: 25%;
        float: left;
        padding: 10px;

        > .inner {
          width: 100%;
          background: #fff;
          padding: 10px;
          box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.1);

          .pic {
            width: 100%;
            img {
              width: 100%;
            }
          }

          h2 {
            font: normal 14px/1.2 "arial";
            color: #777;
            margin-top: 10px;
            margin-bottom: 20px;
          }
        }
      }
    }

    .profile {
      display: flex;
      align-items: center;

      img {
        width: 40px;
        border-radius: 50%;
        margin-right: 10px;
      }
      span {
        cursor: pointer;
        font: 12px/1 "arial";
        color: orange;
        transition: 0.5s;
        &:hover {
          color: aqua;
        }
      }
    }
  }
}

@media screen and (max-width: 1179px) {
  .Gallery article ul li {
    width: 33.333%;
  }
}

@media screen and (max-width: 779px) {
  .Gallery article ul li {
    width: 50%;
  }
}

@media screen and (max-width: 639px) {
  .Gallery article ul li {
    width: 100%;
  }
}
