.Youtube {
  figure {
    background: #ddd;
  }

  article {
    border-top: 1px solid #aaa;
    padding: 20px 0;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    h2 {
      width: 18%;
      font: normal 30px/1 "open sans";
      color: #111;
    }
    .txt {
      width: 38%;
      display: flex;
      flex-wrap: wrap;
      align-content: space-between;
      p {
        font: 12px/1.2 "arial";
        color: #888;
        filter: saturate(0);
      }
      span {
        font: 12px/1 "orbitron";
        letter-spacing: 1px;
        color: orange;
      }
    }
    .pic {
      width: 40%;
      height: 200px;
      background-color: #000;
      filter: saturate(0);
      cursor: pointer;
      transition: 1s;
      &:hover {
        filter: saturate(1.2);
      }
      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }
  }
}

@media screen and (max-width: $tablet) {
  .Youtube {
    article {
      padding: 50px 0px;
      h2 {
        width: 50%;
      }
      .txt {
        width: 50%;
      }
      .pic {
        width: 100%;
        margin-top: 20px;
      }
    }
  }
}

@media screen and (max-width: $mp) {
  .Youtube {
    article {
      h2 {
        width: 100%;
        margin-bottom: 20px;
      }
      .txt {
        width: 100%;
      }
    }
  }
}
