#pics {
  width: 100%;
  height: 100vh;
  background: lightgreen;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-content: center;
  font-size: 10vw;
  color: #555;
  position: relative;
  overflow: hidden;
  gap: 20px;
  padding: 0px 150px;

  p {
    font: bold 200px/1 "arial";
    color: rgba(0, 0, 0, 0.3);
    position: absolute;
    top: 100px;
    left: 100px;
  }

  h3 {
    font: bold 160px/1 "arial";
    color: #fff;
    position: absolute;
    top: 160px;
    left: 100px;
    text-shadow: 20px 20px 50px rgba(0, 0, 0, 0.4);
  }

  .pic {
    width: 18%;
    height: 150px;
    overflow: hidden;
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
}
