.Join {
  figure {
    background: darkseagreen;
  }

  table {
    width: 100%;
    border-top: 2px solid #444;
    border-bottom: 1px solid #444;

    th {
      width: 25%;
      padding: 14px 0px;
      padding-left: 30px;
      text-align: left;
      border-bottom: 1px solid #bbb;

      &:last-child {
        text-align: center;

        input[type="submit"],
        input[type="reset"] {
          width: 150px;
          height: 36px;
          border: none;
          background: #333;
          display: inline-flex;
          justify-content: center;
          align-items: center;
          font: 12px/1 "arial";
          color: #fff;
          margin: 0px 10px;
          cursor: pointer;
          transition: 0.5s;

          &:hover {
            background: hotpink;
          }
        }
      }
    }
    td {
      border-bottom: 1px solid #bbb;
      border-left: 1px solid #bbb;
      padding: 14px 0px;
      padding-left: 30px;

      input[type="text"],
      input[type="password"],
      select,
      textarea {
        border: 1px solid #aaa;
        padding: 8px 20px;
        width: 40%;
        resize: none;
      }

      label {
        margin-right: 4px;
      }
      input[type="radio"],
      input[type="checkbox"] {
        margin-right: 30px;
      }

      .err {
        font: 11px/1 "맑은 고딕";
        color: hotpink;
        margin-left: 20px;
      }
    }
  }
}

@media screen and (max-width: $tablet) {
  .Join {
    table {
      td {
        input[type="text"],
        input[type="password"],
        select,
        textarea {
          width: 80%;
        }

        .err {
          display: block;
          margin-left: 0px;
          margin-top: 10px;
        }
      }
    }
  }
}

@media screen and (max-width: $mp) {
  .Join {
    table {
      th {
        display: none;

        &:last-child {
          display: flex;
          width: 100%;
          justify-content: space-between;
          padding-left: 0px;

          input[type="submit"],
          input[type="reset"] {
            width: 130px;
            margin: 0px;
          }
        }
      }
      td {
        border-left: none;
        padding-left: 0px;
        input[type="text"],
        input[type="password"],
        select,
        textarea {
          width: 100%;
        }
      }
    }
  }
}
